// stylelint-disable font-family-no-missing-generic-family-keyword
//
// o-link-arrow
// --------------------------------------------------


.o-link-arrow {
  font-size: @navbar-font-size;
  font-weight: @brand-font-weight-bold;
  text-decoration: none;

  &:after {
    padding: @o-link-arrow-separator-padding-vertical @o-link-arrow-separator-padding-horizontal;
    font-family: boosted;
    font-size: @o-link-arrow-separator-size;
    color: @o-link-arrow-color;
    content: "\e902"; // Unicode space added since inline-block means non-collapsing white-space
  }
  &:hover,
  &:focus {
    &:after {
      color: @link-hover-color;
      text-decoration: @link-hover-decoration;
    }
  }
  // previous link
  &.back {
    &:after {
      padding: 0;
      content: ""; // Unicode space added since inline-block means non-collapsing white-space
    }
    &:before {
      padding: @o-link-arrow-separator-padding-vertical @o-link-arrow-separator-padding-horizontal @o-link-arrow-separator-padding-vertical 0;
      font-family: boosted;
      font-size: @o-link-arrow-separator-size;
      color: @o-link-arrow-color;
      content: "\e903"; // Unicode space added since inline-block means non-collapsing white-space
    }
    &:hover,
    &:focus {
      &:before {
        color: @link-hover-color;
        text-decoration: @link-hover-decoration;
      }
    }
  }
}
